<template>
    <div class="layout">
        <div  class='layout-menu' :style='menu_style' v-show='menu_show'>
            <LayoutMenu></LayoutMenu>
        </div>
        <div class='layout-main center' :style='main_style'>
            <div class='layout-show-button center' :style='button_style' v-show='button_show' @click='show'>
                <template v-if='!menu_show'>
                    <Icon type="chevron-right" color='white'></Icon>
                </template>
                <template v-else>
                    <Icon type="chevron-left" color='white'></Icon>
                </template>
            </div>
            <LayoutMain></LayoutMain>
        </div>
    </div>
</template>

<script>
import LayoutMenu from './LayoutMenu'
import LayoutMain from './LayoutMain'
import _ from 'lodash'

export default {
    data: function () {
        window._main = this
        return {
            menu_show: false,
            button_show: false,
            is_login: false,
            
            menu_style: {
                width: 0
            },
            main_style: {
                width: '100%'
            },
            button_style: {
                left: 0
            },
            size: 'xs',
        }
    },
    components: {
        LayoutMenu,
        LayoutMain
    },
    methods: {
        change_size: function(width) {
            if (width < 768) {
                this.size = 'xs'
            }
            else if (width < 992) {
                this.size = 'sm'
            }
            else if (width < 1200) {
                this.size = 'md'
            }
            else {
                this.size = 'lg'
            }
        },
        resize: _.debounce(
            function () {
                var width = document.documentElement.clientWidth
                this.change_size(width)
                if (this.size == 'xs' || !this.is_login) {
                    var menu_width = 0
                }
                else {
                    var menu_width = 130
                }
                var main_width = width - menu_width
                this.menu_show = menu_width ? true : false
                this.button_show = (menu_width || !this.is_login) ? false : true
                this.menu_style.width = menu_width + 'px'
                this.main_style.width = main_width + 'px'

                if (this.button_show) {
                    this.menu_style.position = ''
                    this.button_style.left = '0px'
                }
            },
            25
        ),
        // resize: function () {
        //     if (this.button_show) {
        //         this.menu_style.position = ''
        //         this.button_style.left = '0px'
        //     }
        //     var width = document.documentElement.clientWidth
        //     var menu_width = parseInt(width * 0.2)
        //     if (menu_width > 200) {
        //         menu_width = 200
        //     }
        //     else if (menu_width < 140) {
        //         menu_width = 0
        //     }
        //     var main_width = width - menu_width
        //     this.menu_show = menu_width ? true : false
        //     this.button_show = menu_width ? false : true
        //     this.menu_style.width = menu_width + 'px'
        //     this.main_style.width = main_width + 'px'
        // },
        show: function () {
            if (!this.menu_show) {
                this.menu_show = true
                this.menu_style.position = 'absolute'
                this.menu_style.width = '130px'
                this.button_style.left = '130px'
            }
            else {
                this.menu_show = false
                this.menu_style.position = ''
                this.menu_style.width = '0px'
                this.button_style.left = '0px'
            }
        }
    },
    mounted: function () {
        this.resize()
        window.addEventListener('resize', this.resize)
    },
    beforeDestroy: function () {
        window.removeEventListener('resize', this.resize)
    },
    watch: {
        is_login (val) {
            this.resize()
        }
    }
}
</script>

<style>
html {
    overflow: hidden;
    font-size: 14px;
}

.layout {
    background: #f5f7f9;
    height: 100vh;
    width: 100vw;
}

.layout-menu {
    float: left;
    height: 100vh;
    background: #626362;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10;
}

.layout-main {
    float: left;
    height: 100vh;
    z-index: 0;
}

.layout-show-button{
    height: 50px;
    width: 20px;
    float: left;
    z-index: 100;
    position: absolute;
    background: #626362;
}

.center {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    /*align-content: center;*/
}
</style>


